import React from 'react';
import ButtonSubmit from '../ButtonSubmit/ButtonSubmit';
import ordered from './img/ordered.jpg';
import empty from './img/empty.jpg';
import noBookmarks from './img/noBookmarks.jpg';
import noOrders from './img/noOrders.jpg';
import styles from './Info.module.scss';
function Info({ typeInfo, action }) {
	const contentMap = {
		ordered: {
			img: ordered,
			title: ' !',
			subtitle: '  #18     ',
		},
		empty: {
			img: empty,
			title: ' ',
			subtitle: '     ,   .',
		},
		noBookmarks: {
			img: noBookmarks,
			title: '  :(',
			subtitle: '     ',
		},
		noOrders: {
			img: noOrders,
			title: '    ',
			subtitle: ' ?     .',
		},
		nothingHasFound: {
			img: noOrders,
			subtitle: '       ',
		},
	};
	return (
		<div className={styles.info}>
			<div className={styles.container}>
				<div className={styles.avatar}>
					<img src={contentMap[typeInfo].img} alt=" " />
				</div>
				<div className={styles.content}>
					<h2 className={styles.title}>{contentMap[typeInfo].title}</h2>
					<p className={styles.subtitle}>{contentMap[typeInfo].subtitle}</p>
				</div>
				<ButtonSubmit content={' '} reversed action={action} />
			</div>
		</div>
	);
}
export default Info;
